import * as React from 'react';
import { TiArrowSortedDown, TiArrowSortedUp } from 'react-icons/ti';
import { Card, CardBody, CardFooter } from '@material-tailwind/react';
import Pagination from '../Pagination';
import TableSkeleton from './TableSkeleton';

type T_Table = {
  indexList: string[];
  indexKey: string[];
  indexData: any[];
  setPayload: (value: any) => void;
  payload: any;
  maxData: number;
  isLoading?: boolean;
};

const Table: React.FC<T_Table> = ({ indexList, indexKey, indexData, setPayload, payload, maxData, isLoading }) => {
  const [selectMenu, setSelectMenu] = React.useState<string>('10');
  const [activeSort, setActiveSort] = React.useState<string>('');

  React.useEffect(() => {
    sortData('');
  }, []);

  React.useEffect(() => {
    setPayload((prev) => {
      if (prev.page === 1) return { ...prev, size: parseInt(selectMenu) };
      if (prev.page > Math.ceil(maxData / parseInt(selectMenu))) {
        return {
          ...prev,
          size: parseInt(selectMenu),
          page: Math.ceil(maxData / parseInt(selectMenu)),
        };
      };
      return { ...prev, size: parseInt(selectMenu) };
    });
  }, [selectMenu]);

  const sortData = (key, name?) => {
    if (!key.length) return setPayload((prev) => ({ ...prev, sort: key }));
    if (!name) return setPayload((prev) => ({ ...prev, sort: key + ' asc' }));
    if (activeSort === name) {
      setPayload((prev) => ({
        ...prev,
        sort: prev.sort.split(' ')[1] === 'asc' ? key + ' desc' : key + ' asc',
      }));
    } else {
      setPayload((prev) => ({ ...prev, sort: key + ' asc' }));
    };
  };

  const renderSkeleton = () => {
    const arr = [...Array(parseInt(selectMenu))];
    const skeleton = [...arr].map((_, i) => (
      <tr key={i} className={i === parseInt(selectMenu) - 1 ? '' : 'border-b border-blue-gray-50'}>
        <td className="p-2">
          <TableSkeleton />
        </td>
        {indexList.map((_, j) => (
          <td key={j} className="p-3">
            <TableSkeleton />
          </td>
        ))}
      </tr>
    ));
    return skeleton;
  };

  return (
    <React.Fragment>
      <Card className="shadow-none h-full w-full">
        <CardBody className="overflow-x-scroll px-0 py-0 border-b border-blue-gray-100">
          <table className="w-full min-w-max table-auto text-left">
            <thead>
              <tr>
                <th className="w-5 text-center border-b border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors first:rounded-ss-xl last:rounded-se-xl">
                  No
                </th>
                {indexList.map((index, i) => {
                  if (index === 'Action') {
                    return (
                      <th key={i} className="w-14 text-center border-b border-l border-blue-gray-100 bg-blue-gray-50/50 py-4 px-10 transition-colors first:rounded-ss-xl last:rounded-se-xl">
                        {index}
                      </th>
                    );
                  };
                  return (
                    <th
                      key={i}
                      className="cursor-pointer border-b border-blue-gray-100 bg-blue-gray-50/50 p-4 transition-colors hover:bg-blue-gray-50 first:rounded-ss-xl last:rounded-se-xl"
                      onClick={() => {
                        sortData(indexKey[i], index);
                        setActiveSort(index);
                      }}
                    >
                      <span className={activeSort === index ? 'flex gap-x-2 text-blue-500' : 'flex gap-x-2'}>
                        {index}
                        {(activeSort !== index ||
                          payload.sort.split(' ')[1] === 'asc') && (
                            <TiArrowSortedDown
                              size={20}
                            />
                          )}
                        {activeSort === index &&
                          payload.sort.split(' ')[1] === 'desc' && (
                            <TiArrowSortedUp
                              size={20}
                              className={
                                activeSort === index
                                  ? 'cursor-pointer text-blue-500'
                                  : 'cursor-pointer hover:text-gray-500'
                              }
                            />
                          )}
                      </span>
                    </th>
                  );
                })}
              </tr>
            </thead>
            <tbody>
              {isLoading
                ? renderSkeleton()
                : indexData.length !== 0
                  ? indexData.map((data, i) => (
                    <tr key={i} className={i === indexData.length - 1 ? '' : 'border-b border-blue-gray-50'}>
                      <td className="text-center">
                        {parseInt(selectMenu) > 1 ? i + 1 + parseInt(selectMenu) * (payload.page - 1) : i + 1}
                      </td>
                      {indexKey.map((key, j) => {
                        const value = data[key];
                        if (key === 'action') {
                          return (
                            <td key={j} className="w-fit border-l border-blue-gray-50">
                              {value ? value : '-'}
                            </td>
                          );
                        };
                        return (
                          <td key={j} className="p-3 text-wrap">
                            {value ? value : '-'}
                          </td>
                        );
                      })}
                    </tr>
                  ))
                  : (
                    <React.Fragment>
                      <tr>
                        <td className="p-4">-</td>
                      </tr>
                      <tr>
                        <td className="p-4">No data found</td>
                      </tr>
                    </React.Fragment>
                  )}
            </tbody>
          </table>
        </CardBody>
        <CardFooter>
          <Pagination
            indexData={indexData}
            payload={payload}
            setPayload={setPayload}
            selectMenu={selectMenu}
            setSelectMenu={setSelectMenu}
            maxData={maxData}
            loadingState={isLoading}
          />
        </CardFooter>
      </Card>
    </React.Fragment>
  );
};

export default Table;