import * as React from 'react';
import { useNavigate } from 'react-router-dom';
import { MdDelete, MdEditSquare, MdOutlineFileDownload } from 'react-icons/md';
import { IoDuplicate } from 'react-icons/io5';
import { RiFileSearchFill } from "react-icons/ri";
import { format } from 'date-fns';
import { toast } from 'react-toastify';
import { Typography, Button, Dialog, DialogHeader, DialogBody, DialogFooter } from '@material-tailwind/react';
import services from '../../../../../utils/services';
import Table from './Table';
import SearchBar from './SearchBar';
import confirmDelete from '../assets/illustrations/confirm-delete.svg';

export default function JobListTable() {
  const navigate = useNavigate();
  const successNotify = (content) => toast.success(content);
  const errorNotify = (content) => toast.error(content);

  const [openConfirm, setOpenConfirm] = React.useState(false);
  const onOpenConfirm = () => setOpenConfirm((cur) => !cur);

  const [idJob, setIdJob] = React.useState('');
  const [payloadList, setPayloadList] = React.useState({ search: '', page: 1, size: 10, sort: '' });
  const [jobList, setJobList] = React.useState([]);
  const [jobListRender, setJobListRender] = React.useState([]);
  const [totalData, setTotalData] = React.useState<number>(0);
  const [isLoading, setIsLoading] = React.useState(false);
  const titles: string[] = ['Job Name', 'Posted By', 'Assign To', 'Job Posted', 'Deadline', 'Total Application', 'Publication', 'Status Job', 'Action'];
  const keyLists: string[] = ['job_name', 'posted_by', 'assign_to', 'job_posted', 'deadline', 'total_applied', 'publication_type', 'status_job', 'action'];

  React.useEffect(() => {
    getPostingDataList();
  }, [payloadList]);

  React.useEffect(() => {
    setJobListRender(
      jobList.map((item) => {
        return {
          ...item,
          job_posted: item.job_posted ? format(item.job_posted, "dd-MM-yyyy") : '-',
          deadline: item.deadline ? format(item.deadline, "dd-MM-yyyy") : '-',
          total_applied: item.total_applied ? item.total_applied : '-',
          publication_type:
            item.publication_type === 'public' ?
              <span className="bg-green-100 text-green-800 px-2.5 py-0.5 rounded">Public</span> :
              <span className="bg-gray-100 text-gray-800 px-2.5 py-0.5 rounded">Private</span>,
          status_job:
            item.status_job === 'active' ?
              <span className="bg-blue-100 text-blue-800 px-2.5 py-0.5 rounded">Open</span> :
              item.status_job === 'closed' ?
                <span className="bg-red-100 text-red-800 px-2.5 py-0.5 rounded">Closed</span> :
                item.status_job === 'waiting' ?
                  <span className="bg-cyan-100 text-cyan-800 px-2.5 py-0.5 rounded">Waiting</span> :
                  <span className="bg-orange-100 text-orange-800 px-2.5 py-0.5 rounded">Draft</span>,
          action: (
            <div className="flex gap-1 items-center">
              {item.status === 'active' || item.status === 'closed' ?
                <React.Fragment>
                  <RiFileSearchFill
                    size={25}
                    className="text-slate-900 cursor-pointer hover:text-green-700"
                    onClick={() => navigate('/job/posting/detail/' + item.id)}
                  />
                  <IoDuplicate
                    size={25}
                    className="text-slate-900 cursor-pointer hover:text-green-700"
                    onClick={() => navigate('/job/posting/duplicate/' + item.id)}
                  />
                </React.Fragment> :
                <React.Fragment>
                  <MdEditSquare
                    size={25}
                    className="text-slate-900 cursor-pointer hover:text-blue-700"
                    onClick={() => navigate('/job/posting/edit/' + item.id)}
                  />
                  <MdDelete
                    size={25}
                    className="text-slate-900 cursor-pointer hover:text-red-700"
                    onClick={() => { onOpenConfirm(); setIdJob(item.id); }}
                  />
                </React.Fragment>}
            </div>
          ),
        };
      })
    );
  }, [jobList]);

  const getPostingDataList = async () => {
    setIsLoading(true);
    try {
      const result = await services.post('/job/list', { ...payloadList });
      setJobList(result.data);
      setTotalData(result.totalData);
      setIsLoading(false);
    } catch (error) {
      console.error(error);
    };
  };

  return (
    <React.Fragment>
      <div className="flex flex-col p-4 mt-2 gap-5 bg-white !overflow-hidden">
        <div className="flex items-center gap-2">
          <SearchBar setPayload={setPayloadList} />
          <Button variant="outlined" className="border border-gray-500 flex items-center gap-1 h-[44px]">
            <MdOutlineFileDownload className="w-4 h-4" />Download List Job
          </Button>
        </div>
        <Table
          indexList={titles}
          indexKey={keyLists}
          indexData={jobListRender}
          maxData={totalData}
          payload={payloadList}
          setPayload={setPayloadList}
          isLoading={isLoading}
        />
      </div>
      <Dialog className="p-4" size="sm" open={openConfirm} handler={onOpenConfirm}>
        <DialogHeader className="flex justify-center">
          <img src={confirmDelete} alt="Confirm delete" />
        </DialogHeader>
        <DialogBody className="flex flex-col items-center gap-1">
          <Typography variant="paragraph" className="font-bold text-black">Are you sure you want to delete this data?</Typography>
          <Typography variant="small">This action cannot be undone.</Typography>
        </DialogBody>
        <DialogFooter className="flex justify-center gap-2">
          <Button variant="outlined" className="border-gray-400 text-gray-600" onClick={onOpenConfirm}>Cancel</Button>
          <Button
            variant="filled"
            className="bg-danger-500"
            onClick={async () => {
              try {
                const result = await services.delete('/job/posting/' + idJob);
                setPayloadList({ search: '', page: 1, size: 10, sort: '' });
                successNotify('Data has been successfully deleted');
                onOpenConfirm();
              } catch (error) {
                console.error(error);
              };
            }}
          >
            Yes, Delete
          </Button>
        </DialogFooter>
      </Dialog>
    </React.Fragment>
  );
};
