import * as React from 'react';
import { Input, Button } from '@material-tailwind/react';
import { IoClose } from 'react-icons/io5';

type T_SearchBar = {
  className?: string;
  setPayload: (payload: any) => void;
};

const SearchBar: React.FC<T_SearchBar> = ({ className, setPayload }) => {
  const [input, setInput] = React.useState('');

  React.useEffect(() => {
    if (input.length === 0) {
      setDefault();
    };
  }, [input]);

  const setDefault = () => {
    setInput('');
    setPayload((prev) => ({ ...prev, search: '' }));
  };

  const onSearch = () => {
    if (input.length > 0) {
      setPayload((prev) => ({ ...prev, search: input }));
    };
  };

  return (
    <div className={"flex flex-1 items-center " + className}>
      <Input
        labelProps={{ className: "hidden" }}
        size="lg"
        placeholder="Search data here"
        icon={input.length > 0 && <IoClose size={20} className="cursor-pointer hover:text-primary-500" onClick={setDefault} />}
        className="h-full !border-gray-300 placeholder:text-gray-400 placeholder:opacity-100 focus:!outline-none focus:!border-primary-500 rounded-none"
        style={{ borderTopLeftRadius: '0.5rem', borderBottomLeftRadius: '0.5rem' }}
        onChange={(e) => setInput(e.target.value)}
        value={input}
      />
      <Button
        className="!bg-primary-500 rounded-none h-[44px] rounded-r-lg"
        onClick={onSearch}
        disabled={input.length === 0}
      >
        Search
      </Button>
    </div>
  );
};

export default SearchBar;
